import React from "react";

import "./ShortcutOption.css";
import "./ShortcutMenu.css";
import ShortcutOption from "./ShortcutOption";
import { hotkey_mappings } from "../Assets/hotkeys_mappings.js";

function ShortcutMenu({ crm }) {
  let platform = "windows";
  if (window.navigator.platform === "MacIntel") {
    platform = "mac";
  }

  return (
    <div>
      <div className="shortcut-menu">
        <div className="shortcuts">Shortcut</div>
        <div className="shortcut-titles">Title</div>
      </div>
      <div>
        {hotkey_mappings[crm]["hotkeys"].map((shortcut, index) => (
          <ShortcutOption
            num={index + 1}
            key={shortcut["descr"]}
            descr={shortcut["descr"]}
            hotkey={shortcut["hotKey"][platform]}
          />
        ))}
      </div>
    </div>
  );
}

export default ShortcutMenu;
