function packageAPIresponse(valid, msg) {
  return {
    isDisabled: !valid,
    msg: msg,
  };
}

export default async function checkLicenseIsDisabled(license_key, browserID) {
  if (!String(license_key).length) {
    return packageAPIresponse(false, "Input a license key");
  }

  if (String(license_key).length !== 17) {
    return packageAPIresponse(false, "No valid license key found");
  }

  try {
    let licenseRequest = await fetch(
      `https://www.dialfaster.com/api/v1/license/verify/${license_key}/${browserID}`,
      {
        method: "GET",
      }
    );
    if (licenseRequest.status === 404) {
      return packageAPIresponse(true, "404 Error, temporary license given");
    }
    let parsedLicenseRequest = await licenseRequest.json();
    return packageAPIresponse(
      parsedLicenseRequest.valid,
      parsedLicenseRequest.msg
    );
  } catch (err) {
    return packageAPIresponse(false, "Unknown Error");
  }
}
