import "./MainPage.css";
import React from "react";
import ShortcutMenu from "../Components/ShortcutMenu";

import { ArrowRight } from "react-feather";
import Button from "../Components/Button";
import logo from "../Assets/logo.png";

export default function MainPage({
  setPage,
  disabled,
  numClicks,
  errorMsg,
  crm,
}) {
  let content = <ShortcutMenu crm={crm} />;

  if (disabled) {
    let subtext;
    if (errorMsg) {
      subtext = `~${errorMsg}~`;
    }
    content = (
      <div className="disabled-warning">
        <div>Extension Disabled: See Settings Tab</div>
        <div className="disabled-warning-subtext">{subtext}</div>
      </div>
    );
  }

  return (
    <div>
      <div className="title-area">
        <img src={logo} alt="logo" className="logo-img" />
      </div>
      <div className="horizontal-bar">&nbsp;</div>
      {content}
      <div className="horizontal-bar">&nbsp;</div>
      <div className="bottom-area">
        <div>{String(numClicks)} clicks logged</div>
        <div className="settings-button-area">
          <Button
            direction="row-reverse"
            onClick={() => setPage("settings")}
            text="Settings"
            icon={<ArrowRight size={15} />}
          />
        </div>
      </div>
    </div>
  );
}
