import "./SettingsPage.css";
import React, { useState, useEffect } from "react";
import { ArrowLeft, Lock, Unlock, HelpCircle } from "react-feather";
import { version } from "../../package.json";
import Button from "../Components/Button";
import { hotkey_mappings } from "../Assets/hotkeys_mappings";

function contact() {
  let location = "https://www.dialfaster.com/frequently-asked-questions";
  chrome.tabs.create({ active: true, url: location });
}

export default function SettingsPage({
  licenseKey,
  setLicenseKey,
  setCrm,
  crm,
  setPage,
}) {
  const [changesDisabled, setChangesDisabled] = useState(true);

  // dealing with license key input changes
  useEffect(() => {
    chrome.storage.sync.set({ licenseKey: licenseKey }, null);
  }, [changesDisabled, licenseKey]);

  let lock = <Unlock size={15} />;
  if (changesDisabled) {
    lock = <Lock size={15} />;
  }

  return (
    <div>
      <div className="settings-title">
        <h3> Settings </h3>
      </div>
      <div className="horizontal-bar">&nbsp;</div>
      <div className="api-key">
        <div>License Key</div>
        <input
          value={licenseKey}
          onChange={(e) => setLicenseKey(e.target.value)}
          className="api-key-input"
          disabled={changesDisabled}
        ></input>
      </div>
      <div className="api-key">
        <div>Type</div>
        <select
          name="crm"
          value={crm}
          onChange={(e) => setCrm(e.target.value)}
          disabled={changesDisabled}
          className="type-dropdown"
        >
          {Object.keys(hotkey_mappings).map((crm_option) => (
            <option
              key={crm_option}
              value={crm_option}
              disabled={hotkey_mappings[crm_option]["disabled"]}
            >
              {crm_option}
            </option>
          ))}
        </select>
      </div>

      <div className="api-key">
        <div>Version</div>
        <div>{version}</div>
      </div>
      <div className="lock-area">
        <Button
          direction="row-reverse"
          onClick={() => setChangesDisabled(!changesDisabled)}
          text={changesDisabled ? "Unlock to make changes" : "Lock to save"}
          icon={lock}
        />
      </div>
      <div className="horizontal-bar">&nbsp;</div>
      <div className="api-key">
        <Button
          onClick={() => setPage("main")}
          text="Back"
          icon={<ArrowLeft size={15} />}
        />

        <Button
          direction="row-reverse"
          text="Help"
          icon={<HelpCircle size={15} />}
          onClick={() => contact()}
        />
      </div>
    </div>
  );
}
