import "./App.css";
import React, { useState, useEffect } from "react";
import MainPage from "./Pages/MainPage";
import SettingsPage from "./Pages/SettingsPage";
import checkLicenseIsDisabled from "./Utils/checkLicenseKey";

function App(props) {
  const browserID = props.browserID;

  const [page, setPage] = useState("main");
  const [crm, setCrm] = useState(props.crm);
  const [isDisabled, setIsDisabled] = useState(props.isDisabled);
  const [licenseKey, setLicenseKey] = useState(props.licenseKey);
  const [errorMsg, setErrorMsg] = useState(null);

  function onPageClick(newPage) {
    setPage(newPage);
  }

  useEffect(() => {
    checkLicenseIsDisabled(licenseKey, browserID).then((res) => {
      setIsDisabled(res.isDisabled);
      setErrorMsg(res.msg);
    });
  }, [licenseKey, browserID]);

  let content;
  if (page === "main") {
    content = (
      <MainPage
        crm={crm}
        setPage={onPageClick}
        disabled={isDisabled}
        errorMsg={errorMsg}
        numClicks={props.numClicks}
      />
    );
  }

  if (page === "settings") {
    content = (
      <SettingsPage
        crm={crm}
        setCrm={setCrm}
        setPage={onPageClick}
        licenseKey={licenseKey}
        setLicenseKey={setLicenseKey}
      />
    );
  }
  return <div className="main">{content}</div>;
}

export default App;
